@import './../modules';

header{
  position: fixed;  
  width: 100%;
  z-index: 999;
  a{
    position: relative;
    padding: 0.5em 0; 
    margin: 0 0.5em;
    height: 2rem;
    transition: all 0.3s;
    &::after{
      position: absolute;
      content: '';
      bottom: 0;
      right: 0;
      display: block;
      width: 100%;
      height: 2px;
      transform: scaleX(0);
      transform-origin: left;
    }
    &:hover::after{
      transform: scaleX(1);
      transition: transform 0.3s
    }
  }
  div.gnb-depth1{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8.4rem;
    .--left{
      display: inline-flex;
    }
    .gnb-logo{
      display: inline-block;
      height: 8.4rem;
      a::after{
        display: none;
      }
    }
    ul{
      display: inline-flex;
      align-items: center;
      a{
        font-weight: 500;
      }
    }
    ul.gnb-sitemap{
      margin-left: 5.8rem;
      gap: 5.9rem;
      li:last-child{
        display: none;
      }
      li a{
        @include grow_fontw($color-3855FF_ultramarine, 0.4px, 0.3s);
      }
      .focused{
        @include style_font(700, 1.8rem, 1, $color-3855FF_ultramarine);
      }
    }
    ul.gnb-community{
      gap: 5.8rem;
      margin-right: 6.3rem;
      svg path{
        fill: #212121;
      }
      li:first-child a{
        @include grow_fontw($color-008CB2-commu-03, 0.4px, 0.3s);
        &::after{
          background-color: $color-008CB2-commu-03;
        }
      }
      li:last-child a{
        @include grow_fontw($color-3855FF_ultramarine, 0.4px, 0.3s);
        &:hover.icon-open_in_new svg path{
          transition: all 0.3s;
          fill: $color-3855FF_ultramarine;
        }
        &::after{
          background-color: $color-3855FF_ultramarine;
        }
      }
    }
    ul.gnb-user{
      gap: 2rem;
      a{
        @include style_font(400, 1.4rem, 1, #787878);
        margin: 0;
        &::after{
          display: none;
        }
        &:hover{
          color: #666;
          font-weight: 500;
        }
      }
      .focused{
        font-weight: 500;
      }
    }
  }
  div.gnb-depth2{
    width: 100%;
    position: fixed;
    .depth2{
      color: $font_color-white;
      background-color: $color-10319D_royalazure;
      .width1440{
        display: flex;
        padding: 7.6rem 0;
        height: 38rem;
        div{
          margin-right: 239px;
          h5{
            padding-left: 10px;
            font-weight: 700;
            font-size: 24px;
            padding-bottom: 0.5em;
          }
          p{
            width: 266px;
            height: 15rem;
            padding-left: 10px;
            padding-bottom: 3em;
            @include style_font(300, 1.8rem, 1.8em, white);
          }
          button{
            width: 208px;
            height: 47px;
            background: #FFFFFF;
            border-radius: 50px;
            font-weight: 700;
    
            color: $color-10319D_royalazure;
          }
        }
        ul{
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;
          li{
            width: 386px;
            padding-bottom: 1em;
            margin: 20px 48px 40px 0;
            
            font-weight: 700;
            border-bottom: 1px solid rgba(255, 255, 255, 0.07);
            a{
              color: $font_color-white;
            }
          }
        }
      }
    }
  }
}

.depth2{
  display: none;
}
.depth2-open{
  display: block;
  @include animation(0s, 0.6s, fadeIn);
}
// ! 이거 지금 안되고 있음 
.depth2-close{
  @include animation(0s, 0.3s, fadeOut);
}
.depth2-none{
  display: none;
}


header.header-main{
  @include style_header("main");
}
header.header-sub{
  @include style_header("sub");
}