// 🏠️ OSP-5111

@import '../../modules';

main.main-osp5111{
  strong{
    color: $color-2C99FF_azure;
  }
  h4{
    font-weight: 900;
    font-size: 36px;
  }
  section.tmaxos-products{
    padding: 9rem 0;
    text-align: center;
    background: #F8F9FA;
    h3{
      font-weight: 700;
      font-size: 36px;
      line-height: 1.4em;
    }
    div.products{
      padding: 9rem 0;
      ul{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 4rem;
        li{
          width: 45rem; // 31%
          padding: 6rem 3.6rem 3.6rem 3.6rem;
          background: #FFFFFF;
          box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
          border-radius: 3rem;

          .btn-rounded-03{
            width: 13.6rem;
            padding: 1.4rem 0;
          }
          span.product-logo{
            width: 100%;
            height: 3.6rem;
            background-color: gray;
          }
          p{
            padding-top: 3.2rem;
            padding-bottom: calc(2.8rem + 1em);
            font-size: 18px;
            text-align: left;
            color: $font_color-2C2C2C_black;
            line-height: 1.8em;
          }
          div{
            display: flex;
            justify-content: space-between;
            align-items: center;
          }
        }
      }
    }

  }


  section.product{
    .width1440{
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    span{
      width: 664px;
      height: 520px;
      background: gray;
    }
    .feature-content{
      padding: 25.2rem 0;
      width: 452px;
      strong{
        display: block;
        padding-top: 2.4rem;
        padding-bottom: 4.5rem;
      }
      p{
        line-height: 1.8em;
      }

    }
  }



  .product.white{
    .feature-content{
      margin-right: 4rem;
    }
  }

  .product.gray{
    background-color: $bg_main-F8F9FA;
    .feature-content{
      margin-left: 4rem;
    }
    .img_feature-2{

    }
  }
}