// 🏠️ OSP-7121

@import '../../modules';


main.main-osp7121{
  section{
    padding-top: 9.6rem;
  }
  hgroup{
    @include style_font(700, 3.2rem, 1, #2e2e2e);
    padding-bottom: 9.6rem;
    border-bottom: 3px solid $line_color-ECECEC_grey;
    strong{
      color: $color-2C99FF_azure;
    }
    h2{
      padding-bottom: 2rem;
    }
  }

  div.form-layout{
    padding-top: 6.4rem;
    padding-bottom: 9.4rem;
    select{
      appearance: none;
    }
    ul.form-ul{
      display: flex;
      gap: 1.6rem;
      flex-direction: column;

      // ! 동작 정의되고 다시 작업!! 
      li{
        position: relative;
        transition: all 0.3s;
        input[type="checkbox"]{
          appearance: none;
          width: 2.4rem;
          height: 2.4rem;
          border: 1px solid #D8D8D8;
          border-radius: 3px;
          &:checked{
            background-color: $color-2C99FF_azure;
            border: 1px solid $color-2C99FF_azure;
            background-image: url('../../../../public/asset/images/icon/ico_CheckBox.svg');
            background-size: cover;
            background-position: center;
          }

        }
        label{
          visibility: hidden;
          position: absolute;
        }
        textarea::placeholder,
        input::placeholder{
          @include style_font(500, 1.6rem, 1, #91989D);
          transition: all 0.3s;
        }
        > div, 
        > input, 
        select, 
        textarea{
          @include style_font(500, 1.6rem, 1, #91989D);
          width: 25%;
          height: 5.8rem;
          background: #FBFBFB;
          border: none;
          outline: 1px solid #D8D8D8;
          border-radius: 10px;
          padding: 1.6rem 2rem;
          margin-left: 5rem;
          &.full{
            width: 100%;
          }
          &.full.cont{
            vertical-align: text-top;
            height: 50rem;
          }
          label{
            position: relative;
            visibility: visible;
            vertical-align: super;
            padding-left: 1.8rem;
          }
        }


        input.typed{
          // padding-bottom: 1rem;
          &::before{
            font-size: 1.2rem;
            transform-origin: left;
            // transform: translateY(-1.2em) scale(80%);
            transform: translateY(-1.6em);
            opacity: 1;
          }
        }

        &.typed{
          label{
            visibility: visible;

          }
        }

        div.checked, input:focus, textarea:focus{
          outline: 2px solid $color-2C99FF_azure;
        }
        &::after{
          position: absolute;
          content: '';
          top: 2.8rem;
          left: 2rem;
          width: 8px;
          height: 8px;
          background-color: $color-2C99FF_azure;
          border-radius: 50%;
        }
      }
    }
  }
}