// 🏠️ OSP-7111 🏠️ OSP-7116 🏠️ OSP-7120 🏠️ OSP-7126

@import '../../modules';

@mixin board_grid_notice(){
  display: grid;
  grid-template-columns: 10% 78.5% 10%;
  align-items: center;
  gap: 8px;
}
@mixin board_grid_question(){
  display: grid;
  grid-template-columns: 10% 89%;
  align-items: center;
  gap: 8px;
}

.background-01{
  @include style_background-01();
}
// common board style
.main-osp7111,
.main-osp7116,
.main-osp7120,
.main-osp7126{
  section{
    text-align: center;
    h2, h3{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 7.4rem;
    }
  }
  
  .width1352{
    width: 135.2rem;
    margin: 0 auto;
  }
  .custom-board{
    padding: 1.6rem 0 3.8rem 0;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
    background-color: white;
    border-radius: 3rem;
    ul.cont-ul{
      padding: 0 4.4rem;
      li{
        @include board_grid_notice();
        padding: 3rem 0;
        border-bottom: 1px solid #ececec;
        text-align: center;
        div{
          .cate{
            @include style_font(500, 1.4rem, 1, black);
            padding: 1.1rem 2.2rem;
            background-color: #eaeaea;
            border-radius: 3rem;
          }
        }  
        a.cont{
          @include style_font(700, 2rem, 1, #212121);
          position: relative;
          display: inline-block;
          text-align: left;
          margin-left: 15rem;
          .icon-table_new{
            margin-left: 6px;
          }
        }
        a.cont.notice::before{
          position: absolute;
          content: '';
          top: -0.2em;
          left: -5rem;
          width: 2.8rem;
          height: 2.8rem;
          vertical-align: middle;
          background-image: url('../../../../public/asset/images/icon/ico_table_notice.svg');
        }
        .date{
          @include style_font(400, 1.8rem, 1, #797979);
          padding-left: 1em;
        }
      }
    }
    ul.cont-ul.question{
      padding-top: 3.6rem;
      li{
        @include board_grid_question();
        span.cate{
          background-color: #CFE9FD;
        }
        .cont{
          display: inline-flex;
          justify-content: space-between;
        }
      }
    }
    a.link-more{
      @include style_font(500, 1.8rem, 1, $color-2C99FF_azure);
      margin-top: 3.8rem;
      padding: 1rem 0;
      display: inline-block;
      i{
        margin-left: 6px;
      }
    }
  }
  div.search{
    padding-top: 7rem;
    padding-bottom: 2.4rem;
  }

}

main.main-osp7111{
  section.background-01{
    h2{
      padding-bottom: 9.6rem;
    }
    .bg_c-l{
      width: 45.5rem;
      background-size: 200%;
      background-position-y: 150%;
    }
    .bg_c-r{
      width: 113.7rem;
      background-size: 120%;
      background-position-y: -20%;
    }
    ul{
      display: flex;
      gap: 3.9rem;
      justify-content: center;
      z-index: 9;
      li button{
        width: 45.4rem;
        height: 31.6rem;
        padding: 4rem 4.7rem;
        text-align: left;
        background: white;
        box-shadow: 0rem 1rem 4rem rgba(0, 0, 0, 0.05);
        border-radius: 3rem;
        i.icon{
          width: 6.5rem;
          height: 6.5rem;
          background-color: aqua;
        }
        h6{
          padding-top: 3.2rem;
          padding-bottom: 1.8rem;
          @include style_font(700, 2.8rem, 1.4em, #1f1f1f);
        }
        span{
          @include style_font(400, 2rem, 1.6em, #848484);
          width: 28rem;
        }
      }
    }
  }
  section.custom-prods{
    padding-top: 9.8rem;
    padding-bottom: 11.2rem;
    h3{
      padding-bottom: 9.8rem;
    }
    ul{
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 3.9rem;
      li, button{
        width: 45.4rem;
        height: 17.2rem;
        background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
        border-radius: 3rem;
      }
    }
  }
  section.custom-notice{
    padding-top: 9.6rem;
    padding-bottom: 7.6rem;
    background-color: $bg_main-F8F9FA;
  }
  section.custom-question{
    padding-top: 9.6rem;
    padding-bottom: 5.6rem;
    background-color: $bg_main-F8F9FA;
    div.custom-board{
      padding-top: 0;
    }
  }
}

main.main-osp7116 div.custom-board{
  padding-top: 0;
  padding-bottom: 9.6rem;
  box-shadow: none;
  div.custom-board-title{
    @include board_grid_notice();
    span{
      height: 4rem;
      text-align: center;
      background-color: $bg_color-F8F8F8-grey;
      padding: 1rem 0;
      @include style_font(500, 1.6rem, 1.2em, $font_color-797979_grey);
      border-radius: 5px;
    }
  }
  ul.cont-ul{
    padding: 0;
  }
}

main.main-osp7120{
  div.search{
    padding-bottom: 2rem;
  }
  div.custom-board{
    padding-top: 0;

    box-shadow: none;
    padding-bottom: 9.6rem;
  }
  ul.cont-ul.question{
    padding: 0;
  }
}

main.main-osp7126{
  .width1218{
    width: 1218px;
    margin: 0 auto;
  }

  section.content{
    position: relative;
    padding-top: calc(9.6rem - 1em);
    div.background-01{
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
    }
    h2{
      line-height: 1.6em;
    }
    ul.products{
      z-index: 999;
      padding-bottom: 18rem;
      > li{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;

        padding: 5.2rem 6.4rem 4.6rem 5.4rem;
        margin-bottom: 5rem;
        background: #FFFFFF;
        box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
        border-radius: 30px;
      }
      div.download-cont{
        text-align: left;
        flex: 2 2 auto;
        img{
          width: 16rem;
          height: 4rem;
          background-color: gray;
        }
        p{
          @include style_font(400, 1.8rem, 1.6em, #212121);
          width: 59rem;
          padding-top: 1.7rem;
          padding-bottom: 3.6rem;
        }
        div.tmaxgr-spec{
          @include style_font(700, 1.4rem, 1, #91989D);
          ul{
            padding-left: 4rem;
            display: inline-flex;
            flex-wrap: wrap;
            width: 80%;
            li{
              width: 50%;
              padding-bottom: 1rem;
              span.spec-tit{
                width: 6rem;
                font-weight: 700;
              }
              span{
                padding-left: 2.4rem;
                font-weight: 400;
              }
            }
          }
        } 
      }
      div.download-btn{
        a.btn{
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 20.8rem;
          padding: 1.1rem 1.9rem 1.1rem 2.4rem;
          margin-top: 2.4rem;
        }
        .icon-open_in_new{
          width: 1.6em;
          height: 2.4rem;
          svg{
            width: 1.2em;
            height: 2.4rem;
            path{
              fill: white;
            }
          }
        } 
      }
    }
  }
}

