/**
**🍈️ mixin()
** style
** animation
*/

@mixin style_background-01(){
  position: relative;
  padding-top: 9.6rem;
  padding-bottom: 20.5rem;
  width: 100%;
  background-color: $bg_main-F8F9FA;
  z-index: -2;

  .bg_c-l, .bg_c-r{
    position: absolute;
    top: 0;
    height: 100%;
    background-repeat: no-repeat;
    z-index: -1;
  }
  .bg_c-l{
    width: 68.5rem;
    width: 35%;
    left: 0;
    background-position-x: right;
    background-position-y: 200%;
    background-image: url('../../public/asset/images/res/bg_circle_1.svg');
  }
  .bg_c-r{
    width: 61rem;
    width: 30%;
    right: 0;
    background-position-x: left;
    background-position-y: 10%;
    background-image: url('../../public/asset/images/res/bg_circle_2.svg');
  }

}
@mixin style_header($class_name){
  @if $class_name == 'main'{
    background-color: transparent;
    a{
      color: $font_color-white;
    }
    ul.gnb-community svg path{
      fill: white;
    }
    ul.gnb-user a{
      color: rgba(255, 255, 255, 0.58);
    }
    .logo{
      background-image: url('../../public/asset/images/icon/logo_white.svg');
    }
    .gnb-depth1 a{
      &::after{
        background-color: $font_color-white;
      }
      &:hover{
        &::after{
          transform: scaleX(1);
          transition: transform 0.2s ease-in;
        }
      }
    }
  }@else if $class_name == 'sub'{
    background-color: white;
    a{
      color: $font_color-black;
      &:hover{
        color: $color-3855FF_ultramarine;
      }
    }
    .gnb-depth1 a{
      &::after{
        background-color: $color-3855FF_ultramarine;
      }
      &:hover{
        &::after{
          transform: scaleX(1);
          transition: transform 0.2s ease-in;
        }
      }
    }
  }
}

@mixin style_font($weight: 400, $size: 18px, $line_height: 1, $color: black){
  font-weight: $weight;
  font-size: $size;
  line-height: $line_height;
  color: $color;
}

@mixin make_line($width, $height, $color){
  position: relative;

  &::after{
    position: absolute;
// 만드는 중 
    bottom: 0;
    left: 49%;

    content: '';
    width: $width;
    height: $height;
    background: $color;
  }
}

@mixin style_main_h3($color:black){
  h3{
    text-align: center;

    font-weight: 700;
    font-size: 36px;
    line-height: 1;
  
    color: $color;
    div{
      height: 36px;
      &:first-child{
        margin-bottom: 20px;
      }
    }
    strong{
      color: $color-2C99FF_azure;
    }
  }
}

@mixin grow_fontw($color, $size, $time){
  text-shadow: 0 0 0 rgba($color, 0);
  transition: text-shadow $time ease-out;
  &.focused, &:hover{
    color: $color;
    text-shadow: 
    0 -$size 0 $color,
    -$size 0 0 $color,
    $size 0 0 $color;
  }
}

@mixin animation ($delay, $duration, $name) {
  animation-fill-mode: forwards;

  animation-delay: $delay;
  animation-duration: $duration;
  animation-name: $name
}
@mixin keyframe ($animation_name) {
  @keyframes #{$animation_name} {
      @content;
  }
}

@include keyframe (moveUp){
  from{
    opacity: 0.3;
    transform: translateY(5px);
  }to{
    opacity: 1;
    transform: translateY(0);
  }
}
@include keyframe (moveDown){
  from{
    opacity: 0.3;
    transform: translateY(-3px);
  }to{
    opacity: 1;
    transform: translateY(0);
  }
}
@include keyframe (fadeIn){
  from{
    opacity: 0;
  }to{
    opacity: 1;
  }
}
@include keyframe (fadeOut){
  from{
    opacity: 1;
  }to{
    opacity: 0;
  }
}

