/** 
  * 2022.03.21 ~ 
  ** 🛠 reset-scss
  ** @import modules
  ** @import board << layout component's style 
  ** 🍚️ theme
  ** 💫️ icons
  ** 🎈 illust
*/

// reset scss features,, normalizing css settings for all browswer
@import 'scss-reset/_reset.scss';
// *modules: 변수, mixin 패키지 

@import 'modules';
@import './layout/board';

// ? 뭔가 안되네용 ㅠㅠ 😥 
// @include font-face('NotoSansCJKkr-Regular', 'NotoSansCJKkr-Regular', 400);
// $font-stack: NotoSans, sans-serif;

::-webkit-scrollbar{
  width: 0;
  height: 0;
}
html{
  font-size: 10px; // rem 기준
  scroll-behavior: smooth;
}
// * normalize
body{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  // font: 100% $font-stack;
  letter-spacing: -0.5px;
  color: #2E2E2E;
  line-height: 1;
  word-break: keep-all;
  
  min-width: 1440px;

  a{
    text-decoration: none;
    outline: none;
    color:inherit;
    &:hover, &:active{
      text-decoration: none; 
      background-color:transparent;
    }
  }
  span, i{
    display: inline-block;
  }
  input{
    border: 0;
  }
  textarea{
    resize: none;
  }
}

// *🍚️ theme
.App{
  position: relative;
  .width1440{ 
    width: $width_1440;
    margin: 0 auto;
  }    
  // * global 지수빈
  .clear::after{
    clear: both;
    display: block;
    content: '';
  }
  .wrap-img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
    }
  }
  .date{
    color: #B0B0B0;
    font-size: 15px;
  }
  // * global 정수진C 
  // * 버튼 - basic theme
  .btn{
    transition: all 0.3s;
  }
  .btn-rounded-01{
    @include style_font(500, 1.8rem, 1, $color-2C99FF_azure);
    padding: 1.5rem 3rem 1rem 3rem;
    background: white;
    border: 1px solid $color-2C99FF_azure;
    border-radius: 4rem;
    .icon-down{
      vertical-align: middle;
      width: 2.4rem;
      height: 2.4rem;
      background-size: cover;
    }
    &:hover{
      color: $color-007DF1_azure-emp-01;
      border: 1px solid $color-007DF1_azure-emp-01;
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
      border: 1px solid $color-0063C0_azure-emp-02;
    }
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
    }
  }
  .btn-rounded-02{
    @include button_set('basic-rounded-02');
    padding: 1.6rem 6.5rem;
    position: absolute;
    top: -10rem;
    right: 50rem;
  }
  .btn-rounded-03{
    @include style_font(500, 1.8rem, 1.2, white);
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    border-radius: 4rem;
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
      path{
        fill: white;
      }
    }
  }
  .btn-link{
    display: inline-block;
    color: $color-2C99FF_azure;
    font-size: 1.8rem;
    font-weight: 500;
    padding: 1em;
  
    text-align: center;
  }
  .btn-more{ 
    color: $color-0082F3_btn;
  }
  // * 버튼 - commu theme
  .btn.commu{
    display: inline-block;
    height: 4.7rem;
    padding: 1.2rem 3rem 1rem 3rem;
    border-radius: 4rem;
  }
  .btn-rounded-01.commu{
    @include style_font(500, 1.8rem, 1, $color-0AB4E2_commu-01);
    background: #FFFFFF;
    border: 1px solid $color-0AB4E2_commu-01;
    border-radius: 4rem;
    .icon-down{
      vertical-align: middle;
      width: 2.4rem;
      height: 2.4rem;
      background-size: cover;
    }
    &:hover{
      color: #06A1CB;
      border: 1px solid #06A1CB;
    }
    &:active{
      color: $color-008CB2-commu-03;
      border: 1px solid $color-008CB2-commu-03;
    }
  }
  .btn-rounded-03.commu{
    @include style_font(500, 1.8rem, 1.2, white);
    padding: 1.5rem 3rem;
    background-color: $color-0AB4E2_commu-01;
    
    border-radius: 4rem;
    &:hover{
      background: #06A1CB;
    }
    &:active{
      background: $color-008CB2-commu-03;
    }
  }

  // * 검색툴 - basic theme 
  .search{
    margin-left: auto;
    width: 35.7rem;
    // * 검색툴 - basic theme
    input[name='board-search']{
      width: 100%;
      padding: 1.2rem 0 1.2rem 4.7rem;

      color: #91989D;
      font-size: 1.6rem;
      
      border: 1px solid #D8D8D8;
      border-radius: 1rem;
      background: #FBFBFB;
      transition: all 0.3s;

    }
    input[name="board-search"].commu{
      &:focus{
        border: 1px solid $color-0AB4E2_commu-01;
        @include style_font(700, 1.6rem, 1, $font_color-212121_grey);
      }
    }
  }
  // * 검색툴 - commu theme 
  .community-search{
    position: relative;
    &::before{
      position: absolute;
      content: '';
      display: block;
      top: 2rem;
      left: 1.6rem;
      width: 2.2rem;
      height: 2.2rem;
      background-color: gray;
      z-index: 99;
    }

    input[name="search"].commu{
      width: 44.9rem;
      padding: 2.1rem 2rem 2.1rem 6.1rem;
      
      outline: 3px solid rgba($color: #000000, $alpha: 0);
      
      border-radius: 10rem;
      color: #91989D;
      transition: all 0.3s;
      &:hover{
        outline: 3px solid rgba(16, 49, 157, 0.1);
      }
      &:focus{
        outline: 3px solid rgba(16, 49, 157, 0.4);
      }
      &.typing{
        outline: 3px solid rgba(16, 49, 157, 0.3);
        @include style_font(700, 1.8rem, 1, $font_color-212121_grey);
      }
    }
    .btn-close{
      position: absolute;
      right: 2rem;
      top: 2rem;

      width: 2rem;
      height: 2rem;

      background-color: green;
    }
  }

  // * 💫️ 아이콘 
  .icon{
    background-repeat: no-repeat;
    background-size: 100%;
  }
  .icon-down{
    margin-left: 1rem;
    width: 3.4rem;
    height: 3.4rem;
    background-image: url('../../public/asset/images/icon/ico_down.svg');
  }
  .icon-open_in_new{
    margin-left: 4px;
    width: 1.5rem;
    height: 1.5rem;
    background-position: top;
  }
  // .icon-table_notice 
  .icon-table_new{
    width: 2rem;
    height: 2rem;
    vertical-align: middle;
    background-image: url('../../public/asset/images/icon/ico_table_new.svg');
  }
  .icon-more_arrow{
    width: 1.8rem;
    height: 1.1rem;
    background-image: url('../../public/asset/images/icon/ico_more_arrow.svg');
  }
  .icon-table_f_ar_d{
    width: 2.6rem;
    height: 2.6rem;
    background-image: url('../../public/asset/images/icon/ico_table_foling_arrow_down.svg');
  }
  .icon-sort{
    vertical-align: middle;
    margin-left: 1rem;
    width: 1.4rem;
    height: 1.6em;
    background-image: url('../../public/asset/images/icon/ico_sort.svg');
  }

  .logo{
  display: block;
  width: 140px;
  height: 100%;
  background-image: url('../../public/asset/images/icon_logo-blue.png');
  background-repeat: no-repeat;
  background-position: center;
  }
  // * 🎈img 
  .img{
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .img-help-banner{
    position: absolute;
    bottom: 0;
    left: 13.4rem;
    width: 28.9rem;
    height: 35.7rem;
    background-image: url('../../public/asset/images/img_help_banner.png');

  }
}


// *illust
.illust-main-customer{
  background-color: red;
}

div.helloTmax{
  width: 50vw;
  margin: 0 auto;
  text-align: center;
  h1{
    padding: 5rem 0;
    font-size: 3rem;
  }
  h2{
    padding: 2rem 0;
    line-height: 1.4em;
    font-size: 2rem;
  }
  p{
    padding: 2rem 0;
    font-size: 1.4rem;
    line-height: 1.6em;
  }
  ul{
    padding-top: 4rem;
    li{
      padding-top: 1rem;
      display: flex;
      justify-content: space-between;
      a{
        color: blue;
      }
    }
  }
}